							<p>We will only hold your personal information while we are providing you with the Kuīni services, and for a period of [three months] after you stop using Kuīni. We will also require Chatfuel to regularly delete the Kuīni user information it holds on our behalf. Facebook will retain your personal information in accordance with its own policies.</p>
